body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.none{
    display: none;
}

*,
*::after,
*::before{
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
  font-family: 'Monserat arm', Roboto, system-ui, Tahoma, sans-serif;

}

@font-face {
    font-family: "Artsakh font";   /*Can be any text*/
    src: local("Artsakh-Font"),
      url("./fonts/Artsakh-Font.ttf") format("truetype");
  }

a:visited,a{
    color: inherit;

    text-decoration: none;
}
a:hover{
    color: inherit;

    text-decoration: none;
}
a:focus,
a:active{
    outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p{
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
}



ul,
li{
    list-style: none;
}

img,
svg{
    vertical-align: top;
    height: auto;
}

.container{

    padding-left: 86px;
    padding-right: 96px;
}

.swiper-button-prev:after, .swiper-button-next:after{
    font-size: 42px !important;
    color: #121212 !important;
}
.swiper-button-prev {
    left: -76px !important;
}
.swiper-button-next {
    right: -76px !important;
}


