.sectionContent {
    padding: 120px 96px;
    display: flex;
    flex-direction: column;
  }
  .about_sectionContent {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .filterIcon {
    font-size: 48px;
    color: #555555;
    cursor: pointer;
    display: none;
    margin-top: 48px;
    box-shadow: 0 0 0 0.57px #757575;
    border-radius: 6px;
  }
  .filterIcon svg {
    margin-top: 20%;
  }

  
  .section___ {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    color: #121212;
  }
  .section_name {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
    color: #f68d10;
  }
  
  .css-1s2u09g-control {
    align-items: center;
    cursor: default;
    display: flex;
    -webkit-box-flex-wrap: wrap;
    flex-wrap: wrap;
    justify-content: space-between;
    min-height: 38px;
    outline: 0 !important;
    position: relative;
    transition: all 100ms;
    box-sizing: border-box;
    background: #f9f9f9;
    box-shadow: 0px 0px 16px rgba(78, 78, 78, 0.15);
    border-radius: 6px;
  }
  
  .css-319lph-ValueContainer {
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: grid;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    -webkit-box-flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    /* padding: 2px 8px; */
    -webkit-overflow-scrolling: touch;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
  }
  
  .css-1869usk-MuiFormControl-root {
    width: 262px;
  }
  
  .css-hfutr2-MuiSvgIcon-root-MuiSelect-icon {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    width: 2em !important;
    height: 1em;
    display: inline-block;
    fill: currentColor;
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    -webkit-transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-size: 2rem !important;
    position: absolute;
    right: 7px;
    top: calc(50% - 0.5em);
    pointer-events: none;
    color: #121212 !important;
  }
  
  .css-1yk1gt9-MuiInputBase-root-MuiOutlinedInput-root-MuiSelect-root {
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.4375em;
    letter-spacing: 0.00938em;
    color: rgba(0, 0, 0, 0.87);
    box-sizing: border-box;
    position: relative;
    cursor: text;
    display: -webkit-inline-box;
    display: -webkit-inline-flex;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    background: #f9f9f9 !important;
    box-shadow: 0px 0px 16px rgba(78, 78, 78, 0.15) !important;
    border-radius: 6px !important;
  }
  
  .css-11u53oe-MuiSelect-select-MuiInputBase-input-MuiOutlinedInput-input {
    -moz-appearance: none;
    -webkit-appearance: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-radius: 4px;
    cursor: pointer;
    font: inherit;
    letter-spacing: inherit;
    color: red;
    padding: 4px 0 5px;
    border: 0;
    box-sizing: content-box;
    background: none;
    height: 1.4375em;
    margin: 0;
    -webkit-tap-highlight-color: transparent;
    display: block;
    min-width: 0;
    width: 100%;
    -webkit-animation-name: mui-auto-fill-cancel;
    animation-name: mui-auto-fill-cancel;
    -webkit-animation-duration: 10ms;
    animation-duration: 10ms;
    padding: 21px 22px !important;
    font-weight: 700 !important;
    font-size: 16px !important;
    line-height: 130% !important;
    color: #121212 !important;
  }
  
  .css-1869usk-MuiFormControl-root {
    margin: unset !important;
  }
  
  .categoryes {
    display: flex;
    /* flex-direction: column; */
    align-items: center;
  }
  
  .categoryes_title_block {
    margin-bottom: 14px;
  }
  
  .categoryes_title {
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: #121212;
  }
  .empoty_div {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .empoty_div_text {
    margin-top: 60px;
    font-weight: 600;
    font-size: 26px;
    line-height: 130%;
    color: #121212;
  }
  .sectionContent_body {
    margin-top: 22px;
    display: flex;
    justify-content: space-between;
  }
  
  .sectionContent_body_sidebar {
    width: 30%;
    display: flex;
    flex-direction: column;
  }
  
  .price_range_block {
    padding: 32px;
    background: #f9f9f9;
    box-shadow: 0px 0px 16px rgba(78, 78, 78, 0.15);
    border-radius: 6px;
    margin-top: 20px;
  }
  
  .price_range_title {
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: #121212;
  }
  
  .sectionContent_body_products {
    width: 100%;
    display: grid;
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
    margin-left: 50px;
  }
  .sectionContent_div_body_products {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .css-kvsszq-MuiButtonBase-root-MuiPaginationItem-root {
    font-weight: 600 !important;
    font-size: 18px !important;
    line-height: 130% !important;
    color: #555555 !important;
    background: #ffffff !important;
    box-shadow: 0px 0px 16px rgba(78, 78, 78, 0.15) !important;
    border-radius: 8px !important;
    border: none !important;
  }
  
  .css-kvsszq-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected {
    color: #f68d10 !important;
  }
  
  .css-n8417t-MuiSvgIcon-root-MuiPaginationItem-icon {
    font-size: 50px !important;
  
    color: #f68d10;
  }
  
  .css-1oj2twp-MuiPagination-root {
    margin-top: 68px !important;
    margin-left: auto !important;
    margin-right: auto !important;
  }
  
  .css-kvsszq-MuiButtonBase-root-MuiPaginationItem-root {
    padding: 0px 25px !important;
  }
  
  .formInputSearch {
    width: 100%;
    margin-top: 35px;
    padding-top: 31px;
    padding-bottom: 31px;
    padding-left: 48px;
    background: #f9f9f9;
    border-radius: 6px;
    box-shadow: 0px 0px 16px rgb(78 78 78 / 15%);
    outline: none;
    color: #555555;
  }
  .css-1xt5psw {
    background-color: #f9f9f9;
  }
  .filterMobile,
  .filterMobileActiv {
    display: none;
  }
  .about_sectionContent_text{
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: #121212
  }
  @media (max-width: 1170px) {
    /* .sectionContent{
          padding: 20px 30px;
      } */
    .sectionContent_body_products {
      width: 85%;
      grid-template-columns: repeat(auto-fill, minmax(33%, 1fr));
    }
  }
  
  @media (max-width: 991px) {
    .sectionContent_body_products {
      width: 96%;
    }
    #select .MuiFormControl-root .MuiOutlinedInput-root{
      height: 56px !important;
    }
  }
  
  @media (max-width: 840px) {
    .sectionContent_body_products {
      width: 100%;
    }
  }
  @media (max-width: 768px) {
    .about_sectionContent_text{
        margin: auto;
      }
    .heading {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .sectionContent_body_sidebar {
      width: 100%;
    }
    .sectionContent_body_products {
      width: 80%;
      margin-left: unset;
      grid-template-columns: repeat(auto-fill, minmax(40%, 1fr));
    }
    .sectionContent_body {
      flex-wrap: wrap;
    }
    .about_sectionContent {
      flex-wrap: wrap;
    }
    .sectionContent {
      padding: 20px 30px;
    }
    .section___ {
      font-size: 16px;
    }
    .section_name {
      font-size: 16px;
    }
    .categoryes_title_block {
      margin-bottom: 5px;
      margin-top: 20px;
    }
    .formInputSearch {
      padding-top: 20px;
      padding-bottom: 20px;
    }
    .filterIcon {
      display: block;
      font-size: 39px;
    }
    .filterNone {
      display: none;
    }
    .filterMobileActiv {
      display: block;
      width: 100%;
    }
    .categoryes {
      justify-content: space-around;
      align-items: center;
      width: 100%;
    }
    .sectionContent_body_sidebar_none {
      display: none;
    }
    .sectionContent_div_body_products .MuiPagination-root {
      line-height: 57px !important;
    }
    #select .MuiFormControl-root .MuiOutlinedInput-root{
      height: 50px !important;
    }
  }
  
  @media only screen and (max-width: 520px) {
    .sectionContent_body_products {
      width: 60%;
      margin-left: unset;
      grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
    }
  }
  