#div{
    font-size: 50px;
    font-weight: 900;
    height: 500px;
    width: 80%;
    background-color: rgb(224, 218, 218);
    margin: 50px auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.errorImage{
    width: 92%;
}
.errorTitle{
    font-size: 30px;
    color: rgb(131, 109, 109);
}
.errorLink{
    font-size: 35px;
}
.errorLink:hover{
    text-decoration: underline;
    color: rgb(28, 28, 231);
}